.footerpart:hover {
    color: #9f3f69;
    font-weight: bold;
}

.footerbar {
    border-top: 1px solid #fef8e0;
    bottom: 0;
    width: 100%;
    background-color: #111111;
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footerpart {
    color: #d297b1;
    margin-left: 15px;
}

.email-icon {
    display: none;
}

@media (max-width: 1000px) {
    .email-text {
        display: none;
    }

    .email-icon {
        display: inline-block;
    }

    .footerbar {
        flex-direction: row;
        justify-content: space-between;
    }

    .navbar-nav {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
        align-items: center;
        height: 30px;
    }
}
