#navbarNav {
    border-bottom: 1px solid #fef8e0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
    background-color: #111111;
}

.section {
    color: #d297b1;
    font-weight: normal;
    padding-left: 10px;
}
  
.section:hover {
    color: #9f3f69;
    font-weight: bold;
}
  
.section.selected {
    color: #9f3f69;
    font-weight: bold;
}

.navbar-toggler {
    background-color: #111;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
}

.navbar-toggler:hover,
.navbar-toggler:focus {
    color: #111;
    font-weight: bold;
    outline: none; 
}

.navbar-toggler-text {
    color: #d297b1;
    font-weight: bold;
}

.navbar-toggler-text:hover {
    color: #9f3f69;
}

.navbar-toggler-text.spansection {
    display: inline-block;
    vertical-align: middle; 
}
  